<template>
	<div class="container mb-16">

		<div class="d-flex my-5">
			<div class="title-spaced-letter-1">
				Projet : {{ project.name }}
			</div>	
		</div>
		<div class=" mx-auto mt-5"  v-if='imgs.length > 0'>
			<v-carousel height="300" max-height='300' hide-delimiters :show-arrows="true" hide-delimiter-background> 
				<v-carousel-item v-for='(img, i) in imgs' v-bind:key="i" color='primary' :src="img" > 
				</v-carousel-item>
			</v-carousel>
		</div>

		<div class="d-flex justify-space-between align-center mt-5">
			<div class="d-flex">
				<div v-for="categorie in project.categories" v-bind:key="categorie">
					<v-chip :color='(categorie.type == "langs") ? "secondary" : "green"' class="mx-1" size='small'>
					  {{ categorie.name }}
					</v-chip>
				</div>
			</div>

			<div class="d-flex" style="width: fit-content">
				<v-icon class='mx-2' icon="mdi-github" color="white" @click='openLink(project.git_url)'></v-icon>
				<v-icon class='mx-2' icon="mdi-iframe-outline" color="cyan" @click='openLink(project.demo_url)'></v-icon>
			</div>
		</div>

		<div class="ml-4 text-white italic nunito mt-5">
			{{ project.description }}
		</div>
	</div>
</template>

<script src='./js/project-template.js'></script>